import back from '@/components/back'
import { setInterval, clearInterval } from 'timers';

export default {
    components:{
        back
    },
    data(){
        return {
            title: '检查单',
            isEdit: false,
            obj: null,
            ListDetails: [],
            currentdate: '',
            info: '',
            mod: false,
            deadline: 1,
            days: 1,
            Diagnoses: '',
            useDays:'',
            ValidDaysRemark: '',
			ValidDaysRemark2: '',
            PrescriptionNo: '',
            MedicationCompanyId: '',
            MedicationCompanyName: '',
            orderInfo: this.$store.state.pInfo,
            user: this.$store.state.userInfo,
            preId: null,
            showConfirm: false,
            showConfirm2: false,
            popIdx: null,
            timer: null,
            o: null,
			PatientMobile:'',
			saveInput:'',
			saveDr:null,
			BillNo:'',
			PatientSex: '',
			PatientAge:'',
			OtherType:null,
			CensorateOtherAmount:0,
			newCensorateId:null,
			newCensorateName:'',
			item: this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.PatientSex:[],
			itemList:[['男','女']],
			itemShow: false,
			itemAge: this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.age:[],
			itemAgeList:[[]],
			itemAgeShow: false,
			infoPatientName:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.infoPatientName:'',
			modifyPatientInfo:this.$store.state.modifyPatientInfo,
			CensorateId:this.$route.query.CensorateId,
			CensorateName:this.$route.query.CensorateName,
			MismatchShow:false,
        }
    },
    created(){
//         const data = true
//         this.$bridge.callhandler('backConfirm', data)
//         this.timer = setInterval(() => {
//             this.$bridge.registerhandler('confirmBack', (data, responseCallback) => {
//                 responseCallback('cantBack')
//                 this.$refs.back.back()
//             })
//         },50)
      //  this.getDate()
       // this.createPre()
	   this.pull()
        const { saveDrCensorate, checkCkooseList, Diagnoses2,saveInput, saveFlag, pullFlag } = this.$store.state
        this.ListDetails = checkCkooseList
		this.saveInput = saveInput
		this.saveDr = saveDrCensorate	
		console.log(this.ListDetails)
		console.log(saveInput)
		for (var i = 1; i < 100; i++) {//1~99年龄循环
			this.itemAgeList[0].push(i)
		}
    },
	computed:{
	    totalPrice(){
	        let p = 0
	        if(this.ListDetails.length){
	            this.ListDetails.forEach(v => {
	                p += (+v.ItemPrice) * v.ItemNum
	            })
	        }
	        return p.toFixed(2)
	    },
		ConsumablesMoney(){
		    let p = 0
		    if(this.ListDetails.length){
				if(this.OtherType == 0){
					this.ListDetails.forEach(v => {
					    p += (+v.ItemOtherAmount) * v.ItemNum
					})
				}else if(this.OtherType == 1){
					p = Number(this.CensorateOtherAmount)
				}else if(this.OtherType == 2){
					this.ListDetails.forEach(v => {
					    p += (+v.ItemOtherAmount) * v.ItemNum
					})
					p = Number(p) + Number(this.CensorateOtherAmount)
				}else if(this.OtherType == 3){
					let arr = []
					this.ListDetails.forEach(v => {
						arr.push((+v.ItemOtherAmount))
					})
					p = Math.max.apply(null, arr);
				}
		        
		    }
		    return p.toFixed(2)
		},
		totalMoney(){
			 let p = Number(this.totalPrice) + Number(this.ConsumablesMoney)
			 return p.toFixed(2)
		}
	},
    mounted(){
//         this.$refs.back.flag = false
//         this.$refs.back.flag2 = true
			if(this.saveInput){
				this.ValidDaysRemark2 = this.saveInput.ValidDaysRemark2
				this.Diagnoses = this.saveInput.Diagnoses
				this.ValidDaysRemark = this.saveInput.ValidDaysRemark
			}
			if(this.CensorateId){
            	this.switchOrganization()
        	}
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
		switchOrganization(){
            this.$post('InspectionAPI/Censorate/SaveDrCensorate',{ //保存医生合作检查机构
                DrId:this.user.Data.User.DoctorId,
                DrName:this.user.Data.User.DoctorName,
                CensorateId:this.CensorateId,
                CensorateName:this.CensorateName
            }).then(o => {
                this.MismatchShow = true
            })
        },
		infoPatientVal(){
			const inpEl = this.$refs.infoP
			inpEl.focus()
			  var length = inpEl.value.length
			  if (document.selection) {
				var range = inpEl.createTextRange()
				range.moveStart('character', length)
				range.collapse()
				range.select()
			  } else if (typeof inpEl.selectionStart === 'number' && typeof inpEl.selectionEnd === 'number') { 
				inpEl.selectionStart = inpEl.selectionEnd = length
			  }
		},
		goModifyPatientInfo(){
			let arr = {
				infoPatientName:this.infoPatientName,
				PatientSex:this.item,
				age:this.itemAge
			}
			this.$store.dispatch('pushmodifyPatientInfo', arr)
		},
		pull(){
			 this.$post('InspectionAPI/Censorate/CensorateInit',{
			    PatientId: this.orderInfo.PatientInfo.PatientId
			}).then(o => {
				this.info = o
 				this.PatientMobile = o.PatientMobile
				this.BillNo = o.BillNo
// 				this.PatientSex = o.PatientSex
// 				this.PatientAge = o.PatientAge
				if(!this.modifyPatientInfo){
					this.infoPatientName = o.PatientName
					this.item[0] = o.PatientSex == 1?'男':o.PatientSex == 2?'女':'未知'
					let age = o.PatientAge.split('岁')
					this.itemAge[0] = age[0]
					this.$nextTick(() => {
						this.itemShow = true
						this.itemAgeShow = true
						setTimeout(()=>{
							this.itemShow = false
							this.itemAgeShow = false
						},100)
					})
				}
			})
			this.$get('InspectionAPI/Censorate/QueryDefaultCensorate',{ //获取医生合作检查机构
			   DrId: this.user.Data.User.DoctorId,
			}).then(o => {
			    this.OtherType = o.OtherType
				this.CensorateOtherAmount = o.CensorateOtherAmount
				this.newCensorateId = o.CensorateId
				this.newCensorateName = o.CensorateName
			})
		},
		onChange(val){
			const val_ = val.join('')
			if(val_.indexOf('岁') === -1 && (val_.indexOf('天') != -1 || val_.indexOf('月') != -1)){
				this.itemAge[0] = 1
			}
			
		},
		saveInputVal(){
			let saveInputVal = {
				ValidDaysRemark2: this.ValidDaysRemark2,
				Diagnoses: this.Diagnoses,
				ValidDaysRemark: this.ValidDaysRemark
			}
			this.$store.dispatch('pushsaveInput',saveInputVal)
		},
        operate(type, i, idx){
            if(type === 'del' && i.ItemNum > 1){
                i.ItemNum -= 1
            }
            else if(type === 'add'){
                i.ItemNum = +i.ItemNum + 1
            }
			this.ListDetails.splice(idx, 1, i)
			this.$store.dispatch('pushcheckCkooseList',this.ListDetails)
        },
		saveList(){
			if(this.isEdit && this.ListDetails.length > 0){
				return this.$toast('请先保存您编辑的信息')
			}
			this.$store.dispatch('pushisShowXC',true)
			this.$store.dispatch('pushisUp',0)
			this.goModifyPatientInfo()
			this.$router.push(`/inspect/pharmacy`)
		},

        pop(idx){
            this.showConfirm = true
            this.popIdx = idx
        },
        confirm(){
            this.ListDetails.splice(this.popIdx, 1)
			if(this.ListDetails.length == 0){
				this.$store.dispatch('pushsaveDrCensorate',null)
			}
		},
		saveCommonlUsed(){
			if(!(this.ListDetails && this.ListDetails.length)){
			    return this.$toast('请添加检查项')
			}
			if(this.isEdit && this.ListDetails.length > 0){
				return this.$toast('请先保存您编辑的信息')
			}
			let IsUp = 0
			this.ListDetails.forEach(v=>{
				if(v.IsUpper){
					IsUp = 1
				}
			})
			if(IsUp){
				return this.$toast('有已下架的检查项，请重新选择！')	
			}
			const List = []
            this.ListDetails.forEach(v => {
                List.push({
					CensorateItemId:v.CensorateItemId,
					CensorateItemName:v.CensorateItemName,
					ItemNum:v.ItemNum
                })
            })
			this.$post('InspectionAPI/Censorate/SaveDrCensorateUse',{ //保存常用
			   	DrId: this.user.Data.User.DoctorId,
			   	DrName:this.user.Data.User.DoctorName,
			  	CensorateId:this.newCensorateId,
				CensorateName:this.newCensorateName,
				AllNum:this.ListDetails.length,
				ClinicDesc:this.ValidDaysRemark2,
				ClinicImpression:this.Diagnoses,
				SaveDrCensorateItems: List,
			}).then(o => {
				this.$toast('保存成功')
			})
		},
        send(){
// 			 if(!this.Diagnoses){
// 			    return this.$toast('请填写临床拟诊信息')
// 			}
// 			if(!this.ValidDaysRemark2){
// 			    return this.$toast('请填写临床情况说明')
// 			}
			if(!(this.ListDetails && this.ListDetails.length)){
			    return this.$toast('请添加检查项')
			}
			if(this.isEdit && this.ListDetails.length > 0){
				return this.$toast('请先保存您编辑的信息')
			}
			if(!this.BillNo){
			    return this.$toast('网络故障，请尝试重新添加检查项')
			}
			if(!this.infoPatientName){
				return this.$toast('请填写姓名')
			}
			if(this.item[0] != '男' && this.item[0] != '女'){
				return this.$toast('请选择性别')
			}
			if(Number(this.itemAge[0]) < 1){
				return this.$toast('请选择年龄')
			}
			let IsUp = 0
			this.ListDetails.forEach(v=>{
				if(v.IsUpper){
					IsUp = 1
				}
			})
			if(IsUp){
				return this.$toast('有已下架的检查项，请重新选择！')	
			}
            const List = []
            this.ListDetails.forEach(v => {
                List.push({
					CensorateItemId:v.CensorateItemId,
					CensorateItemName:v.CensorateItemName,
					ItemNum:v.ItemNum
                })
            })
            const ApplyId = this.orderInfo && this.orderInfo.Method ? this.orderInfo.Id : null
            const MPrescriptionId = this.orderInfo && this.orderInfo.PrescriptionId ? this.orderInfo.PrescriptionId : this.$route.query.MPrescriptionId
            this.$post('InspectionAPI/Censorate/SaveCensorateRecord',this.$pick({
				DrId: this.user.Data.User.DoctorId,
				DrName: this.user.Data.User.DoctorName,
				CensorateId:this.newCensorateId,
				PatientId: this.orderInfo.PatientInfo.PatientId,
				PatientName: this.infoPatientName,
				PatientMobile: this.PatientMobile,
				PatientSex: this.item[0]=='男'?1:this.item[0]=='女'?2:0,
				PatientAge: this.itemAge[0],
				BillNo:this.BillNo,
				ClinicDesc:this.ValidDaysRemark2,
				ClinicImpression:this.Diagnoses,
				DoctorRemark: this.ValidDaysRemark,
				SaveCensorateItems: List,
				
            })).then(o => {
				if(this.BillNo){
					this.$router.replace(`/inspect/onsuccess?name=${this.infoPatientName}&NO=${this.BillNo}`)
				}
//                 if(o.IsNeedSign){
//                     this.$bridge.callhandler('signId', o.UniqueId, (data) => {
//                         this.$router.replace(`/onsuccess?name=${this.orderInfo.PatientInfo.PatientName}&NO=${this.BillNo}`)
//                     })
//                 }
//                 else{
//                     this.$router.replace(`/onsuccess?name=${this.orderInfo.PatientInfo.PatientName}&NO=${this.BillNo}`)
//                 }
            })
// 			if(this.saveDr){
// 				this.$post('InspectionAPI/Censorate/SaveDrCensorate',{ //保存医生合作检查机构
// 					DrId: this.user.Data.User.DoctorId,
// 					DrName: this.user.Data.User.DoctorName,
// 					CensorateId:this.saveDr.CensorateId,
// 					CensorateName:this.saveDr.CensorateName
// 				 }).then(o => {
// 					
// 				 })
// 			}
			
			
        }
    }
}



